import "./App.css";
import React from "react";
import { useState, useEffect } from "react";
import * as tf from "@tensorflow/tfjs";

function App() {
  const [val, setVal] = useState();
  const [filename, setFilename] = useState("SELECT AN IMAGE");
  const [model, setModel] = useState(null);
  const [file, setFile] = useState(null);
  const [selectedImage, setSelectedImage] = useState();
  const [hasimage, setHasImage] = useState(false);
  const [hasPrediction, setHasPrediction] = useState(false);

  useEffect(() => {
    const loadModel = async () => {
      const model_url = "KModel/model.json";
      const model = await tf.loadLayersModel(model_url);
      setModel(model);
    };

    loadModel();
  }, []);

  const handleSubmit = async (event) => {
    event.preventDefault();
    let image = document.getElementById("upimage");

    let tensorImg = tf.browser
      .fromPixels(image)
      .resizeNearestNeighbor([224, 224])
      .toFloat()
      .expandDims();
    console.log(file.name);
    try {
      var prediction = await model.predict(tensorImg).data();
      console.log(prediction);
      setHasPrediction(true);
      if (prediction[0] === 1) {
        setVal("This is a good eye");
      } else if (prediction[1] === 1) {
        setVal("This is level 1");
      } else if (prediction[2] === 1) {
        setVal("This is level 2");
      } else if (prediction[3] === 1) {
        setVal("This is level 3");
      } else if (prediction[4] === 1) {
        setVal("This is level 4");
      } else if (prediction[5] === 1) {
        setVal("I think it's a dog");
      } else if (prediction[6] === 1) {
        setVal("I think it's a dog");
      } else if (prediction[7] === 1) {
        setVal("I think it's a dog");
      } else if (prediction[8] === 1) {
        setVal("I think it's a dog");
      } else if (prediction[9] === 1) {
        setVal("I think it's a dog");
      } else {
        setVal("This is Something else");
      }
    } catch (error) {
      console.log(error);
    }
  };
  const handleFileUpload = (event) => {
    const infile = event.target.files[0];
    setFile(infile);
    setFilename(infile.name);
    setSelectedImage(URL.createObjectURL(event.target.files[0]));
    setHasImage(true);
  };

  const resetAll = () => {
    setHasImage(false);
    setHasPrediction(false);
    setFilename("SELECT AN IMAGE");
  };

  return (
    <div className="items-center ">
      <h1 className=" flex items-center justify-center text-3xl font-extrabold dark:text-indigo-800 md:text-5xl lg:text-5xl">
        <span className="text-transparent bg-clip-text bg-gradient-to-r to-blue-900 from-blue-900">
          Intelligent system to detect Diabetic Retinopathy
        </span>
      </h1>
      <p className="text-lg font-normal text-white lg:text-xl">
        Upload the image file to detect.
      </p>

      {/* <h1 className="flex items-center justify-center">Eye</h1> */}

      <form onSubmit={handleSubmit}>
        {hasimage ? (
          <div className=" flex items-center justify-center">
            <img
              id="upimage"
              alt="not found"
              width={"250px"}
              src={selectedImage}
            ></img>
          </div>
        ) : (
          <div className="flex w-full items-start justify-center bg-grey-lighter mb-5 mt-[5rem] ">
            <label className="w-64 flex flex-col items-center px-4 py-6 bg-white text-blue rounded-lg shadow-lg tracking-wide uppercase border border-blue cursor-pointer hover:bg-blue hover:text-blue-600">
              <svg
                className="w-8 h-8"
                fill="blue"
                xmlns="http://www.w3.org/2000/svg"
                viewBox="0 0 20 20"
              >
                <path d="M16.88 9.1A4 4 0 0 1 16 17H5a5 5 0 0 1-1-9.9V7a3 3 0 0 1 4.52-2.59A4.98 4.98 0 0 1 17 8c0 .38-.04.74-.12 1.1zM11 11h3l-4-4-4 4h3v3h2v-3z" />
              </svg>
              <span className="mt-2 text-base leading-normal">{filename}</span>
              <input
                type="file"
                name="file"
                className="hidden"
                onChange={(e) => {
                  setFile(e.target.files[0]);
                  handleFileUpload(e);
                }}
              />
            </label>
          </div>
        )}

        <span className="text-white">File Uploaded : {filename}</span>

        {hasimage ? (
          <div className="flex items-center justify-center">
            <button
              className="flex bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full mt-5"
              type="submit"
            >
              PREDICT
            </button>

            <span>
              <p className="text-white">Gap</p>
            </span>

            <button
              className="flex bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-full mt-5"
              onClick={resetAll}
            >
              CLEAR
            </button>
          </div>
        ) : (
          <div></div>
        )}
      </form>
      <span> </span>
      {hasPrediction ? (
        <div className=" flex items-center justify-center text-2xl">
          <span className=" text-transparent  bg-clip-text bg-gradient-to-r to-blue-900 from-blue-900 font-black">
            Detected Image is : {val}
          </span>
        </div>
      ) : (
        <div></div>
      )}
    </div>
  );
}

export default App;
